.introducing {
	padding: 164px 0 75px;
	@include md {
		padding: 100px 0; }
	@include sm {
		padding: 43px 0; }
	&__inner {
		max-width: 960px; }
	&__title {
		&:not(:last-child) {
			margin-bottom: 26px; } }
	&__sm-description {
		line-height: (26/13); }
	&__diagram {
		margin: 91px auto 0;
		padding: 0 15px;
		max-width: 1250px;
		// height: 670px
		svg {
			width: 100%;
			display: block;
			border: 1px solid; } } }
