.info-block {
	padding: 154px 0 100px;
	color: $white;
	background: $black;
	@include lg {
		padding: 100px 0 60px; }
	@include sm {
		padding: 50px 0 28px; }
	&__header {
		margin: 0 auto 68px;
		max-width: 970px;
		text-align: center;
		@include lg {
			margin-bottom: 30px;
			max-width: 720px; }
		@include sm {
			margin-bottom: 40px; }
		p {
			opacity: 0.8; } }
	&__title {
		margin-bottom: 21px;
		@include lg {
			margin-bottom: 22px; }
		@include sm {
			margin-bottom: 17px; } }
	&__logo {
		display: inline-block;
		vertical-align: top;
		margin-bottom: 11px;
		width: 48px;
		@include sm {
			margin-bottom: 22px; } }
	&__content {
		margin: 0 auto 86px;
		max-width: 797px;
		@include lg {
			margin-bottom: 60px; }
		@include sm {
			margin-bottom: 30px; } }
	&__graph {
		padding: 20px 0;
		margin: 0 auto;
		max-width: 1000px;
		@include lg {
			padding: 40px 0;
			max-width: 597px; }
		@include sm {
			padding: 18px 0;
			max-width: 100%; } } }
