.member {
	padding: 20px 15px;
	text-align: center;
	&__img {
		margin-bottom: 25px;
		&-in {
			width: 110%;
			transform: translate3d(-5%,0,0); } }
	&__title {
		margin-bottom: 13px;
		@include f(16, 19, null, 700); }
	&__description {
		margin-bottom: 13px;
		color: $lightGray;
		@include f(16, 24); }
	&__social {
		font-size: 0;
		a {
			display: inline-block;
			vertical-align: top;
			padding: 10px;
			color: $white;
			font-size: 24px;
			@include hover {
				svg {
					fill: darken(#BEBEBE, 10%); } }
			svg {
				fill: #BEBEBE;
				transition: fill 0.175s ease-in-out; } } } }
