.sm-graph {
	&_green {
		.sm-graph {
			&__title {
				color: $green; }
			&__line {
				&:before, &-in {
					background: $green; } } } }
	&_blue {
		.sm-graph {
			&__title {
				color: $blue; }
			&__line {
				&:before, &-in {
					background: $blue; } } } }
	&__title {
		margin-bottom: 20px;
		@include f(18, 22, null, 700);
		@include lg {
			margin-bottom: 50px; }
		@include sm {
			margin-bottom: 28px;
			@include f(16, 24); } }
	&__container {
		display: table;
		table-layout: fixed;
		width: 100%; }
	&__row {
		display: table-row; }
	&__box {
		display: table-cell;
		min-width: 67px;
		@include lg {
			width: 100% !important;
			margin-bottom: 4px; }
		&:nth-child(even) {
			.sm-graph {
				&__line {
					&:before, &-in {
						opacity: 0.5; } } } } }
	&__container,
	&__row,
	&__box {
		position: relative;
		@include lg {
			display: block; } }
	&__line {
		position: relative;
		padding: 0 1px;
		height: 11px;
		opacity: 0.5;
		@include lg {
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			height: auto; }
		&:before {
			content: '';
			position: absolute;
			top: 0;
			left: 1px;
			right: 1px;
			bottom: 0;
			@include lg {
				display: none; } }
		&-in {
			display: none;
			height: 100%;
			@include lg {
				display: block; } } }
	&__info {
		position: relative;
		padding: 8px 15px 0 1px;
		@include clr;
		@include lg {
			padding: 0;
			height: 60px;
			font-size: 0;
			line-height: 60px; } }
	&__value,
	&__description {
		padding: 5px 0;
		@include lg {
			display: inline-block;
			vertical-align: middle;
			padding: 0; } }
	&__value {
		@include f(16, 24, null, 700);
		@include lg {
			padding: 0 0 0 18px;
			width: 70px;
			@include f(18, 24); } }
	&__description {
		@include f(14, 22);
		@include lg {
			padding: 0;
			width: 350px;
			@include f(16, 22); }
		@include sm {
			width: 210px;
			@include f(14, 20); } } }
