.topic {
	position: relative;
	padding: 220px 0 210px;
	text-align: center;
	@include md {
		padding: 220px 0 110px; }
	@include sm {
		padding: 88px 0 45px; }
	&__logo {
		img {
			max-width: 100%; }
		@include sm {
			margin: 0 auto;
			width: 50px; } }
	&__title {
		margin-top: 25px;
		@include f(32, 37, null, 700);
		letter-spacing: 0.08em;
		@include sm {
			margin-top: 29px;
			@include f(26, 20); } }
	&__sub-title {
		margin-top: 12px;
		@include sm {
			margin-top: 25px;
			font-size: 12px; } }
	&__text {
		margin-top: 40px;
		font-size: 18px;
		@include sm {
			margin-top: 32px;
			font-size: 16px; } }
	&__subscription {
		margin-top: 25px;
		@include sm {
			margin-top: 17px; } }
	&__info {
		margin-top: 65px;
		@include sm {
			margin-top: 32px;
			max-width: 205px; } }
	&__creators {
		margin-top: 31px;
		@include md {
			margin-top: 37px; }
		@include sm {
			margin-top: 41px; } } }

.info {
	position: relative;
	padding-left: 22px;
	vertical-align: top;
	letter-spacing: 0.17em;
	font-size: 12px;
	color: rgba($white, .5);
	text-transform: uppercase;
	display: inline-block;
	@include sm {
		padding: 0; }
	&__icon {
		position: absolute;
		top: -1px;
		left: 0;
		@include sm {
			position: relative;
			top: 0;
			text-align: center; }
		.icon {
			font-size: 20px; } } }
.creators {
	&__text {
		margin-top: 10px;
		@include f(12, 24);
		letter-spacing: 0.17em;
		text-transform: uppercase;
		@include md {
			margin-top: 0;
			display: block; } }
	&__text,
	&__list {
		vertical-align: top;
		display: inline-block;
		&:not(:first-child) {
			margin-left: 40px;
			@include md {
				margin-left: 0; } } }
	&__list {
		@include md {
			margin-top: 10px;
			display: block; } }
	&__item {
		vertical-align: middle;
		font-size: 0;
		display: inline-block;
		&:not(:first-child) {
			margin-left: 22px;
			@include sm {
				margin-left: 7px; } }
		&_text {
			font-size: 14px; } } }

.clouds {
	position: absolute;
	top: 0;
	left: 50%;
	right: 0;
	bottom: 0;
	transform: translateX(-50%);
	width: 2034px;
	& > div {
		position: absolute; }
	img {
		display: block; }
	&__top {
		top: 0;
		left: 50%;
		width: 100vw;
		transform: translateX(-50%);
		img {
			width: 100%; } }
	&__bottom-left {
		left: -35px;
		bottom: -502px; }
	&__bottom-right {
		right: 34px;
		bottom: -458px; } }
