.features {
	padding: 100px 0;
	text-align: center;
	@include md {
		padding: 53px 0; }
	@include sm {
		padding: 40px 0; }
	&__inner {
		max-width: 1036px; }
	&__title {
		&:not(:last-child) {
			margin-bottom: 26px; } }
	&__sm-description {
		margin: 0 auto;
		max-width: 970px; }
	&__wrap {
		@include clr;
		&:not(:first-child) {
			margin-top: 80px;
			@include md {
				margin-top: 98px; }
			@include md {
				margin-top: 53px; } } }
	&__col {
		float: left;
		margin-bottom: 50px;
		padding: 0 60px;
		width: 50%;
		@include md {
			margin-bottom: 47px;
			float: none;
			width: 100%; }
		@include sm {
			margin-bottom: 28px;
			padding: 0;
			font-size: 14px;
			line-height: (22/14); } }
	&__ico {
		&:not(:last-child) {
			margin-bottom: 18px;
			@include md {
				margin-bottom: 14px; }
			@include sm {
				margin-bottom: 19px; } } }
	&__title-md {
		@include f(26, 24, null, 400);
		color: $shamrock;
		&:not(:last-child) {
			margin-bottom: 15px;
			@include md {
				margin-bottom: 18px; }
			@include sm {
				margin-bottom: 21px;
				font-size: 20px; } } } }
