}

.btn-menu__wrap:before {
  top: 0;
  transform-origin: top left;
}

.is-open .btn-menu__wrap:before {
  transform: translate3d(4px, -0.5px, 0) rotate(45deg);
}

.btn-menu__wrap:after {
  bottom: 0;
  transform-origin: bottom left;
}

.is-open .btn-menu__wrap:after {
  transform: translate3d(4px, 1px, 0) rotate(-45deg);
}

.btn-menu__wrap .btn-menu__line {
  top: 6px;
}

