$titleWidth: 37.2;

.info-list {
	&__item {
		padding: 16px 0;
		@include clr;
		@include sm {
			padding: 10px 0; } }
	&__title {
		width: $titleWidth + %;
		color: $blue;
		@include f(12, 24);
		letter-spacing: 0.2em;
		text-align: right;
		text-transform: uppercase;
		@include sm {
			@include f(12, 22);
			text-align: left; } }
	&__description {
		width: 100 - $titleWidth + %;
		@include f(16, 24);
		text-align: left;
		@include sm {
			padding-top: 4px;
			@include f(14, 22); } }
	&__title,
	&__description {
		float: left;
		padding-left: 15px;
		padding-right: 15px;
		@include sm {
			padding-left: 0;
			padding-right: 0;
			width: 100%; } }
	&__title,
	&__description p {
		padding-top: 5px;
		padding-bottom: 5px;
		@include sm {
			padding-top: 2px;
			padding-bottom: 2px; } } }
